.NavBar {
   position: fixed;
   top: 0;
   left: 0;
   display: -ms-flexbox;
   display: flex;
   -ms-flex-pack: justify;
       justify-content: space-between;
   width: 97.6vw;
   height: 10vh;
   padding: 0 1.2vw;
   z-index: 2;
   background-color: rgb(224, 193, 255);
   border-bottom: 0.1px solid #c3bbbb;
}

@media(max-width: 599px){
   .NavBar {
      display: none;
   }
}


