.Button {
   outline: none;
   background-color: rgb(224, 211, 236);
}

Button:hover {
   cursor: pointer;
}

.settingsBtn {
   width: 1.8vw;
   height: 1.8vw;
   margin-left: 0.2vw;
   border-radius: 50%;
   background-color: rgb(234, 214, 255);
}

.navItemBtn {
   height: 10vh;
   padding: 0 3vw;
   background-color: inherit;
   border: none;
   text-decoration: none;
   color: black;
   font-size: 1.05em;
}

.navItemBtn:hover {
   -webkit-transform: scale(1.1);
       -ms-transform: scale(1.1);
           transform: scale(1.1);
}

.submitBtn {
   display: block;
   margin: 2vh 0;
   padding: 1vh 1vw;
   border-width: 2px;
   font-size: 1.05em;
   -webkit-box-shadow: 0.1vw 0.2vh black, -0.05vw -0.05vh black;
           box-shadow: 0.1vw 0.2vh black, -0.05vw -0.05vh black;
}

.selectedBtn {
   font-weight: bold;
   background-color: rgb(224, 193, 255);
}

.togglerBtn {
   float: right;
   padding: 0.75vh 1.25vw;
   font-size: 0.95em;
   -webkit-box-shadow: 0.1vw 0.2vh black, -0.05vw -0.05vh black;
           box-shadow: 0.1vw 0.2vh black, -0.05vw -0.05vh black;
   background-color: rgb(239, 220, 255);
   border-top-left-radius: 8px;
   border-bottom-left-radius: 8px;
   border-top-right-radius: 8px;
   outline: none;
}

.itemBtn {
   display: block;
   margin: 0 auto;
   padding: 1vh 2vw;
   font-size: 1em;
   background-color: rgb(224, 193, 255);
   -webkit-box-shadow: 0.1vw 0.2vh black, -0.05vw -0.05vh black;
           box-shadow: 0.1vw 0.2vh black, -0.05vw -0.05vh black;
}

.deleteBtn {
   opacity: 0;
   background-color: inherit;
   border: none;
   color: red;
   font-size: 1.2em;
   margin: auto 0;
   -webkit-transition: opacity 0.5s ease-in;
   -o-transition: opacity 0.5s ease-in;
   transition: opacity 0.5s ease-in;
}

@media(max-width: 599px){
   .Button {
      padding: 1vh 2vw;
      background-color: rgb(224, 193, 255);
   }

   .submitBtn {
      padding: 1.5vh 3vw;
      font-size: 0.9em;
      width: 98%;
   }

   .settingsBtn {
      -ms-flex-positive: 1;
          flex-grow: 1;
      height: inherit;
      margin-left: 1vw;
      border-radius: 8px;
   }

   .selectedBtn {
      background-color: rgb(207, 160, 255)
   }

   .navItemBtn {
      padding: 0 3vw;
      background-color: inherit;
      font-size: 1.3em;
   }

   .deleteBtn {
      opacity: 1;
   }
}



