.App, body, html {
   margin: 0;
   padding: 0;
   margin-bottom: 3vh;
   font-family: "roboto";
   background: rgb(255, 221, 235);
   background: -webkit-linear-gradient(to right, rgb(255, 221, 235), rgb(237, 214, 255));
   background: -webkit-gradient(linear, left top, right top, from(rgb(255, 221, 235)), to(rgb(237, 214, 255)));
   background: -webkit-linear-gradient(left, rgb(255, 221, 235), rgb(237, 214, 255));
   background: -o-linear-gradient(left, rgb(255, 221, 235), rgb(237, 214, 255));
   background: linear-gradient(to right, rgb(255, 221, 235), rgb(237, 214, 255));
}


