.SideDrawer {
   position: fixed;
   top: 12vh;
   left: 0;
   width: 65vw;
   height: 95vh;
   padding-top: 5vh;
   z-index: 2;
   display: -ms-flexbox;
   display: flex;
   -ms-flex-direction: column;
       flex-direction: column;
   background: -webkit-gradient(linear, left top, right top, from(rgb(255, 221, 235)), to(rgb(237, 214, 255)));
   background: -webkit-linear-gradient(left, rgb(255, 221, 235), rgb(237, 214, 255));
   background: -o-linear-gradient(left, rgb(255, 221, 235), rgb(237, 214, 255));
   background: linear-gradient(to right, rgb(255, 221, 235), rgb(237, 214, 255));
   -webkit-transition: -webkit-transform 0.45s ease-out;
   transition: -webkit-transform 0.45s ease-out;
   -o-transition: transform 0.45s ease-out;
   transition: transform 0.45s ease-out;
   transition: transform 0.45s ease-out, -webkit-transform 0.45s ease-out;
}

.OpenDrawer {
   -webkit-transform: translateX(0);
       -ms-transform: translateX(0);
           transform: translateX(0);
}

.OpenDrawer button {
   -webkit-transition: opacity 2s;
   -o-transition: opacity 2s;
   transition: opacity 2s;
   opacity: 1;
}

.ClosedDrawer {
   -webkit-transform: translateX(-100%);
       -ms-transform: translateX(-100%);
           transform: translateX(-100%);
}

.ClosedDrawer button {
   -webkit-transition: opacity 0.33s;
   -o-transition: opacity 0.33s;
   transition: opacity 0.33s;
   opacity: 0;
}

.SideDrawer .ActiveNav button {
   background-color: inherit;
   -webkit-transform: scale(1.1);
       -ms-transform: scale(1.1);
           transform: scale(1.1);
}

@media(min-width: 600px){
   .SideDrawer {
      display: none;
   }
}


