.Toolbar {
   position: fixed;
   top: 0;
   left: 0;
   display: -ms-flexbox;
   display: flex;
   -ms-flex-pack: justify;
       justify-content: space-between;
   padding: 0 2vw;
   width: 96vw;
   height: 12vh;
   z-index: 2;
   background-color: rgb(224, 193, 255);
   border-bottom: 1px solid gray;
}

@media(min-width: 600px){
   .Toolbar {
      display: none;
   }
}


