.Modal {
   position: fixed;
   top: 33.3vh;
   left: 37.5vw;
   width: 25vw;
   height: 33.3vh;
   padding: 1vh 1vw;
   font-size: 1.1em;
   background-color: rgb(224, 193, 255);
   border-radius: 4px;
   -webkit-box-shadow: 4px 4px 20px;
           box-shadow: 4px 4px 20px;
   z-index: 8;
}

.Modal h3 {
   font-size: 1.3em;
}

.Confirmation .loader {
   padding: 0;
}

@media(max-width: 599px){
   .Modal {
      left: 8vw;
      width: 70vw;
      padding: 5vw;
      height: -webkit-fit-content;
      height: -moz-fit-content;
      height: fit-content;
      font-size: 1.3em;
   }

   .Modal h3 {
      margin-top: 0;
   }
}


