.IconList {
   list-style: none;
   padding: 0;
   margin-top: 8vh;
   width: 95%;
   display: -ms-flexbox;
   display: flex;
   -ms-flex-direction: column;
       flex-direction: column;
}

.IconList h3 {
   margin-top: 0;
   margin-left: 0.85vw;
   font-size: 2em;
}

.IconList ul {
   display: -ms-flexbox;
   display: flex;
   -ms-flex-wrap: wrap;
       flex-wrap: wrap;
   margin: 0;
   padding: 0;
}

@media(max-width: 599px){
   .IconList {
      width: 100%;
   }
   
   .IconList h3 {
      margin-left: 10%;
      font-size: 2.2em;
   }

   .IconList ul {
      padding: 0 3vw;
      -ms-flex-pack: space-evenly;
          justify-content: space-evenly;
   }
}


